/* AboutMe.module.css */
.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 40px;
    font-family: 'Arial', sans-serif;
    color: #333;
}

.section {
    margin-bottom: 40px;
}

.section h2 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #007bff;
}

.section p {
    font-size: 16px;
    line-height: 1.6;
}

.card {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-info {
    margin-top: 40px;
    border-top: 2px solid #007bff;
    padding-top: 20px;
}

.contact-info p {
    font-size: 16px;
    margin-bottom: 10px;
}

@media (max-width: 768px) {
    .container {
        padding: 20px;
    }

    .section h2 {
        font-size: 22px;
    }

    .section p {
        font-size: 14px;
    }

    .card {
        padding: 15px;
    }
}

@media (max-width: 480px) {
    .container {
        padding: 10px;
    }

    .section h2 {
        font-size: 20px;
    }

    .section p {
        font-size: 12px;
    }

    .card {
        padding: 10px;
    }

    .contact-info p {
        font-size: 14px;
    }
}
